.header {
  box-sizing: border-box;
  position: relative;
  display: flex;
  justify-content: center;
  max-width: 1600px;
  height: 800px;
  background-color: $dark-color;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;

  &__wrapper {
    width: 1070px;
  }

  &__nav {
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 22px;
  }

  &__inner-wrapper {
    margin-top: 163px;
    display: flex;
  }
}

.nav {
  display: flex;
  align-items: center;

  &__logo {
    background-image: url("../images/logo/dia-logo.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    width: 45px;
    height: 25px;
  }

  &__item {
    margin-right: 64px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    @include font-poppins(13px, bold, 150%, #fff);
    display: block;
    position: relative;
    text-align: center;
    letter-spacing: 3px;
    text-transform: uppercase;
    transition: 0.3s;
    
    &-underline::after,
    &:hover::after {
      content: "";
      display: block;
      position: absolute;
      width: 100%;
      bottom: -8px;
      height: 1px;
      background: $active-color;
      border-radius: 4px;
    }
  }
}

.strategic {

  &__heading {
    @include font-poppins(64px, 600, 68px, #fff);
    width: 490px;
  }

  &__text {
    @include font-open-sans(18px, normal, 150%, #fff);
    width: 362px;
  }

  &__button {
    @include font-poppins(15px, normal, 22px, #fff);
    text-align: center;
    background-color: $active-color;
    border-radius: 8px;
    width: 226px;
    height: 50px;
    margin-top: 83px;
    border: 0;
    transition: 0.3s;

    &:hover {
      transform: scale(1.025);
      background-color: #fff;
      color: $active-color;
    }
  }

}

.slider {
  position: absolute;
  width: 687px;
  height: 680px;
  overflow: hidden;
  bottom: 0;
  right: 0;
  border-radius: 30px 0px;

  // &__arrow {
  //   display: block;
  //   position: absolute;
  //   bottom: 0;
  //   background-image: url("../images/slider/arrow-right.svg");
  //   background-position: center;
  //   background-size: contain;
  //   background-repeat: no-repeat;
  //   width: 16px;
  //   height: 16px;
  // }

  &__intro {
    position: absolute;
    display: inline-block;
    bottom: 55px;
    left: 144px;
  }

  &__buttons {
    position: absolute;
    display: inline-block;
    background-image: url("../images/slider/Arrow\ buttons.svg");
    width: 32px;
    height: 32px;
    bottom: 60px;
    left: 48px;
  }

  .right {
    transform: rotate(180deg);
    margin-left: 8px;
    left: 88px;
  }
}

.slider img {
  margin: 0;
  padding: 0;
  width: 687px;
  height: 687px; // у фото снизу пустая область, поэтому высота чуть больше родительского блока
  object-fit: cover;
  object-position: center;
  border-radius: 30px 0px;
}

.intro__heading {
  @include font-poppins(13px, bold, 19px, #fff);
  text-transform: uppercase;
  letter-spacing: 3px;
  margin: 0;
  margin-bottom: 8px;
}

.intro__text {
  @include font-open-sans(14px, normal, 140%, #fff);
  margin: 0;
}